<template>
  <!-- 参赛信息 -->
  <div class="info">
    <div class="info__cont">
      <div class="info__cont--row">
        <div class="item">
          <div class="name">{{user.username}}</div>
          <div class="tel">{{userPhone}}</div>
        </div>
        <div class="item">
          <div class="cord">{{user.winsnum}}胜/{{user.failurenum}}败</div>
          <router-link to="/list" class="find__btn"></router-link>
        </div>
      </div>
      <div class="info__cont--btn" @click="showModal=2"></div>
    </div>
    <transition-group name="cover">
      <div v-show="showModal==1" class="mask invitation" key="a" @touchmove.prevent>
        <div class="box">
          <div class="title">蔡文姬邀请你加入战队</div>
          <div class="btns">
            <div class="btn no" @click="jumpToMatch(false)"></div>
            <div class="btn yes" @click="jumpToMatch(true)"></div>
          </div>
        </div>
      </div>
      <div v-show="showModal==2" class="mask create" key="b" @touchmove.prevent>
        <div class="box">
          <div class="title">创建战队</div>
          <div class="cont">
            <Input v-model="teamName" type="text" placeholder="请输入战队名称" size="large" clearable :maxlength="10" />
            <!-- <input type="text" placeholder="请输入战队名称" v-model="teamName" @input="changeInput"> -->
            <div class="tips" :class="{'err':errFlag}">{{errTxt}}</div>
          </div>
          <div class="btns">
            <div class="btn cancel" @click="closeModal(2)"></div>
            <div class="btn confirm" @click="create"></div>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>
<script>
import mixin from '../../mixins/mixins.js';
import { Local } from 'js/utils.js';

const Interval1 = 5000; //邀请状态

export default {
  name: 'info',
  props: [],
  data() {
    return {
      userId: Local.get('user_id') || '',
      userPhone: Local.get('user_phone') || '',
      user: {
        username: '',
      },
      showModal: 0,
      teamName: '',
      errFlag: false,
      errTxt: '名称应为中、英文，最长10个字符',
      asUserTeamRel: '',
      timer1: null, //团员受邀轮询
    };
  },
  mixins: [mixin],
  components: {

  },
  created() {
    let that = this;
    //首次获取一次
    that.getUserInfo(); //获取受邀状态
    that.creatTimer();
  },
  mounted() {

  },
  destroyed() {
    let that = this;
    that.clearTimer();
  },
  computed: {

  },
  methods: {
    creatTimer() {
      let that = this;
      if (!that.timer1) {
        that.timer1 = setInterval(() => {
          that.getUserInfo(); //获取受邀状态
        }, Interval1)
      }
    },
    clearTimer() {
      let that = this;
      if (that.timer1) { //如果定时器在运行则关闭
        clearInterval(that.timer1);
        that.timer1 = null;
      }
    },
    closeModal(flag) {
      let that = this
      if (flag == 1) {

      } else if (flag == 2) {
        that.teamName = ''
        that.errFlag = false
        that.errTxt = '名称应为中、英文，最长10个字符'
      }
      that.showModal = 0
    },
    // changeInput() {
    //   //   var that = this
    // },
    /**
     * 获取个人信息
     */
    getUserInfo() {
      let that = this;
      let phone = Local.get('user_phone');
      if (phone && phone != '') {
        //判断信息是否完善
        that.$api.SelectUserInfo(phone).then(res => {
          if (Number(res.code) == 10000) {
            let data = res.data;
            // console.log(data)

            if (!data.userInfo.phone || data.userInfo.phone == '') {
              that.$Message.success('请先登录');
              Local.clear();
              //放到生命周期中
              setTimeout(() => {
                that.$router.push('/login');
              }, 500)
              return;
            }
            
            if (data.userInfo.username == '' || data.userInfo.schoolname == '' || data.userInfo.major == '' || data.userInfo.classname == '' || data.userInfo.studentcode == '') {
              that.$Message.success('请先完善个人信息');
              setTimeout(() => {
                that.$router.push('/fillinfo');
              }, 500)
              return;
            }

            //是否处于战队邀请或已加入战队
            if (data.isteam) {
              if (Number(data.asUserTeamRel.status) == 0) {
                //被邀请状态
                that.showModal = 1;
                that.asUserTeamRel = data.asUserTeamRel;
                that.clearTimer();
              } else if (Number(data.asUserTeamRel.status) == 1) {
                //已在团队
                Local.set('team_id', data.asUserTeamRel.teamid);
                that.$router.push('/match');
              }
            }
            that.user = data.userInfo
          } else {
            that.$Message.warning(res.msg);
          }
        })
      } else {
        that.$Message.success('请先登录');
        setTimeout(() => {
          that.$router.push('/login');
        }, 500)
      }
    },
    /**
     * 创建战队
     */
    create() {
      let that = this
      let reg = /^[\u4e00-\u9fa5|a-z|^\s]{1,10}$/

      if (that.teamName == '') {
        that.errFlag = true
        that.errTxt = '战队名称不能为空'
        return
      } else if (!reg.test(that.teamName)) {
        that.errFlag = true
        that.errTxt = '名称应为中、英文，最长10个字符'
        return
      } else {
        that.errFlag = false
        that.errTxt = ''

        that.$api.CreateTeam(that.userId, that.teamName).then(res => {
          // console.log(res)
          if (Number(res.code) == 10000) {
            that.$Message.success('操作成功');
            Local.set('team_id', res.data);
            setTimeout(() => {
              that.$router.push('/match');
            }, 500)
          } else {
            that.$Message.warning(res.msg);
          }
        })
      }
    },
    /**
     * 接收邀请
     */
    jumpToMatch(type) {
      let that = this;
      let teamid = that.asUserTeamRel.teamid;
      let userTeamRelId = that.asUserTeamRel.id;
      that.$api.DoUserTeamRelInfo(userTeamRelId, type).then(res => {
        if (Number(res.code) == 10000) {
          if (type == true) {
            that.$Message.success('加入成功');
            Local.set('team_id', teamid);
            setTimeout(() => {
              that.$router.push('/match');
            }, 500)
          } else {
            that.$Message.warning('您拒绝了邀请');
            that.showModal = 0;
            that.creatTimer();
          }
        } else {
          that.$Message.warning(res.msg);
        }
      })
    }
  },

};

</script>
<style lang="scss" scoped>
.info {
  position: relative;
  min-height: 100vh;
  background: url('~img/competition_bg.png') center center / 100% 100% no-repeat;

  &__cont {
    position: absolute;
    left: 50%;
    bottom: 20%;
    transform: translateX(-50%);
    width: 80%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &--row {
      padding: 15px 0;

      .item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px 0;

        .name {
          font-size: 19px;
          font-weight: bold;
          color: #958F9D;
          line-height: 25px;
        }

        .tel {
          font-size: 14px;
          font-weight: 500;
          color: #958F9D;
          line-height: 25px;
        }

        .cord {
          font-size: 16px;
          font-weight: 500;
          color: #ACABD7;
          line-height: 25px;
        }

        .find__btn {
          display: block;
          width: 140px;
          height: 37px;
          margin-top: 10px;
          transition: all .2s ease-in-out 0s;
          background: url('~img/competition_btn_lssg.png') center center / 100% 100% no-repeat;

          &:active {
            opacity: .5;
            // transform: scale(1.1);
          }
        }
      }
    }

    &--btn {
      width: auto;
      height: 53.5px;
      flex-shrink: 0;
      margin-top: 10px;
      transition: all .2s ease-in-out 0s;
      background: url('~img/competition_btn_cjzd.png') center center / 100% 100% no-repeat;

      &:active {
        opacity: .5;
        // transform: scale(1.1);
      }
    }
  }

  //弹层
  .mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 203;

    .btns {
      display: flex;
      justify-content: space-between;
      margin-top: 15px;

      .btn {
        width: 98px;
        height: 39px;
        flex-shrink: 0;
        transition: all .2s ease-in-out 0s;
        background-size: 100% 100%;
        background-position: center center;
        background-repeat: no-repeat;

        &:active {
          opacity: .5;
          // transform: scale(1.1);
        }

        &+.btn {
          margin-left: 35px;
        }

        &.no {
          background-image: url('~img/btn_yq.png');
        }

        &.yes {
          background-image: url('~img/btn_js.png');
        }

        &.cancel {
          background-image: url('~img/btn_cancel.png');
        }

        &.confirm {
          background-image: url('~img/btn_establish.png');
        }
      }

    }

    &.invitation {
      margin-top: 70px;

      .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        padding: 10px 60px;
        height: 112px;
        background: url('~img/competition_bg_invitation.png') center center / 100% 100% no-repeat;

        .title {
          font-size: 16px;
          font-weight: 500;
          color: #FDF2C6;
          line-height: 19px;
          text-align: center;
        }

      }
    }

    &.create {
      background-color: rgba(0, 0, 0, 0.85);

      .box {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 289px;
        // background: linear-gradient(-90deg, rgba(51, 44, 48, 1), rgba(40, 38, 52, 1), rgba(40, 38, 52, 1), rgba(51, 44, 48, 1));
        background: url('~img/pop_bg_02.png') center center / 100% 100% repeat-y;
        // border: 1px solid #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 25px;

        &::before,
        &::after {
          content: '';
          position: absolute;
          left: 0;
          width: 100%;
          height: 33.5px;
          // border: 1px solid;
        }

        &::before {
          top: -32.5px;
          background: url('~img/pop_bg_01.png') center center / 100% 100% no-repeat;
        }

        &::after {
          bottom: -32.5px;
          background: url('~img/pop_bg_03.png') center center / 100% 100% no-repeat;
        }

        .title {
          font-size: 16px;
          font-weight: bold;
          color: #ACA4C9;
          margin-bottom: 10px;
          text-align: center;
        }

        .cont {
          margin-top: 10px;

          /deep/ .ivu-input-wrapper {
            .ivu-input-icon {
              height: 41px;
              line-height: 41px;
            }

            input {
              width: 100%;
              height: 41px;
              background: #FFFBF7;
              border-radius: 4px;
              font-size: 16px;
              padding: 0 12.5px;
              border: 0;
              outline: none;

              ::placeholder {
                color: #CEC9CD;
              }
            }
          }

          .tips {
            height: 25px;
            line-height: 25px;
            font-size: 13px;
            color: #958F9D;

            &.err {
              color: #E84E39;
            }
          }
        }
      }
    }
  }
}


.cover-enter-active,
.cover-leave-active {
  opacity: 1;
  transition: 0.3s ease-out;
}

.cover-enter {
  transform: scale(1.2);
  opacity: 0;
}

.cover-leave-to {
  opacity: 0;
  transform: scale(1);
}

</style>



